.works {
  background-color: #151515;
  color: white;

  position: relative;
  height: 100%;
  width: 100%;
  font-family: primary-reg;

  .row,
  .categories {
    display: flex;

    margin-bottom: 7em;
  }

  .row {
    opacity: 0;
  }

  .categories {
    margin: 4em 6em;
    justify-content: center;
    button {
      transform: translateX(-1000px);
      opacity: 0;
      border: 1px solid #fff;
      border-radius: 10px;
      padding: 0.8em 1.2em;
      margin-left: 4em;
      color: #fff;
      font-weight: bold;
      transition: color 2s cubic-bezier(0.075, 0.82, 0.165, 1);

      &:hover {
        color: #151515;
      }
    }
  }
}
