* {
  box-sizing: border-box;
}

ul {
  margin: 0;
  list-style-type: none;
  padding: 0;
}

body {
  &::-webkit-scrollbar {
    display: none;
  }
  cursor: none;
  height: 100%;
  width: 100%;
  font-family: "primary-reg", sans-serif;
  margin: 0;
  padding: 0;
  color: #252525;
  overflow: hidden;
}
html {
  scroll-behavior: smooth;
}
.page-container {
  display: flex;
  flex-direction: column;

  .flex {
    display: flex;
  }
}

a {
  text-decoration: none;
}

#circle {
  width: 80px;
  height: 80px;
  border: 1px solid #3c2946;
  position: fixed;
  border-radius: 50%;
}

#scroll {
  width: 100%;
  height: 100vw;
  overflow: auto;
}
