.skill-box {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  .img {
    img {
      width: 100px;
    }
  }

  .minor-skills {
    margin-left: 1em;
    h3 {
      padding: 0;
      margin: 0;
      margin-bottom: 1em;
      font-size: 1.5em;
    }
  }
  .icons {
    display: flex;
    justify-content: space-around;
    i {
      font-size: 1.8em;
    }
  }
}
